@font-face {
  font-family: "HelveticaRegular";
  src: url("../fonts/HelveticaRegular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Oswald";
  src: url("../fonts/Oswald-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  width: 100%;
  height: 100%;
  font-size: 100%;
  font-family: "Oswald";
  font-size: 14px;
  font-weight: 500;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: "Oswald";
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Oswald";
}

@media (max-width: 992px) {
  html {
    font-size: 12px;
  }
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1400px;
}

.product-card {
  padding: 94px 60px 84px;
}

@media (max-width: 1200px) {
  .product-card {
    padding: 40px 0 0;
  }
}

.product-card__inner {
  display: flex;
}

@media (max-width: 1200px) {
  .product-card__inner {
    flex-direction: column;
  }
}

@media (max-width: 1200px) {
  .product-card__row {
    margin-bottom: 12px;
  }
}

.product-card__column:last-child {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  margin-left: 20px;
}

@media (max-width: 1200px) {
  .product-card__column:first-child {
    display: flex;
    justify-content: space-around;
  }
}

@media (max-width: 992px) {
  .product-card__column:first-child {
    flex-direction: column;
    margin-bottom: 12px;
  }
}

.product-card__tabs {
  display: flex;
  flex-direction: column;
}

.product-card__slider {
  margin-bottom: 32px;
}

@media (max-width: 992px) {
  .product-card__slider {
    justify-content: center;
  }
}

.product-card__title {
  margin-bottom: 34px;
  font-size: 22px;
}

.product-card__btns {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  text-align: center;
}

.product-card__btn {
  background: #000;
}

.product-card__btn:last-child {
  background: #33F0A9;
}

.product-card__btn:not(:last-child) {
  margin-bottom: 20px;
}

.product-card__btn-add {
  background: #33F0A9;
}

.product-card__btn-build {
  color: #33F0A9;
  background: #000;
}

.btn {
  display: block;
  padding: 24px 8px;
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  transform: translate(-10px, -10px);
  transition: transform 0.4s ease 0s;
  cursor: pointer;
}

.btn:hover {
  transform: translate(0);
}

.slider {
  position: relative;
  display: flex;
}

.slider__item {
  border: 2px solid #E3E2E2;
  width: 203px;
  height: 166px;
  transition: border 0.4s ease 0s;
  cursor: pointer;
}

.slider__item:hover, .slider__item_active {
  border: 2px solid #33F0A9;
}

.slider__item img {
  height: 100%;
  max-width: 100%;
}

.slider__preview-imgs {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@media (max-width: 768px) {
  .slider__preview-imgs {
    display: none;
  }
}

.slider__container {
  overflow: hidden;
  margin-left: 16px;
  border: 2px solid #E3E2E2;
  width: 539px;
  height: 539px;
}

@media (max-width: 768px) {
  .slider__container {
    margin: 0;
    width: 100%;
  }
}

@media (max-width: 576px) {
  .slider__container {
    height: 350px;
  }
}

.slider__wrapper {
  display: flex;
  height: 100%;
  transition: all 0.8s ease 0s;
}

.slider__slide {
  height: 100%;
  cursor: pointer;
}

.slider__slide img {
  height: 100%;
  max-width: 100%;
  transition: transform 0.4s ease 0s;
}

.slider__slide img:hover {
  transform: scale(1.3);
}

.slider__paginations {
  position: absolute;
  left: 50%;
  bottom: 16px;
  display: none;
  transform: translateX(-50%);
}

@media (max-width: 768px) {
  .slider__paginations {
    display: flex;
  }
}

.slider__dot {
  list-style: none;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  background: #000;
  opacity: 0.2;
  transition: opacity 0.8s ease 0s;
  cursor: pointer;
}

.slider__dot:not(:last-child) {
  margin-right: 16px;
}

.slider__dot_active {
  opacity: 1;
}

.characteristics__inner {
  display: flex;
}

@media (max-width: 1200px) {
  .characteristics__inner {
    flex-direction: column;
  }
}

@media (max-width: 992px) {
  .characteristics__inner {
    flex-flow: row wrap;
    justify-content: space-between;
    margin: 0;
  }
}

.characteristics__item {
  padding: 12px 6px;
  max-width: 147px;
  text-align: center;
  background: #33F0A9;
}

@media (min-width: 1200px) {
  .characteristics__item:not(:last-child) {
    margin-right: 42px;
  }
}

@media (max-width: 1200px) {
  .characteristics__item {
    margin: 0 0 16px 16px 0;
  }
}

@media (max-width: 576px) {
  .characteristics__item {
    margin-left: 0;
    min-width: 100%;
  }
}

.characteristics__title {
  margin-bottom: 8px;
  font-size: 18px;
  text-transform: uppercase;
}

.characteristics__descr {
  font-family: "Helvetica";
  font-size: 16px;
  line-height: 140%;
}

.options {
  flex-grow: 1;
}

.options__item:not(:last-child) {
  margin-bottom: 80px;
}

@media (max-width: 1200px) {
  .options__item:not(:last-child) {
    margin-bottom: 40px;
  }
}

.options__discr {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 18px;
}

.options__discr::after {
  content: '';
  position: absolute;
  bottom: -8px;
  width: 100%;
  height: 2px;
  background: #000;
}

.options__price {
  font-size: 24px;
}

.options__price span {
  margin-left: 8px;
  color: #F14237;
}

@media (max-width: 1248px) {
  .options__price span {
    margin: 0;
  }
}

.options__price_discount {
  position: relative;
}

@media (max-width: 1248px) {
  .options__price_discount {
    display: flex;
    flex-direction: column;
  }
}

.options__price_discount::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  width: 50%;
  height: 2px;
  background: #F14237;
}

@media (max-width: 1248px) {
  .options__price_discount::before {
    top: 27%;
    width: 100%;
  }
}

.options__row-glasses {
  display: flex;
}

.options__column {
  position: relative;
  cursor: pointer;
}

.options__column:hover .options__prompt {
  display: block;
  min-width: 120px;
}

.options__column:not(:last-child) {
  margin-right: 8px;
}

.options__column img {
  margin-bottom: 8px;
  width: 53px;
  height: 44px;
}

.options_sale {
  position: relative;
}

.options_sale::before {
  content: "Sale";
  position: absolute;
  right: 0;
  top: 0;
  padding: 0 8px;
  font-size: 10px;
  line-height: 18px;
  text-transform: uppercase;
  color: #fff;
  background: #F14237;
}

.options__tab_active {
  border: 2px solid #F14237;
}

.options__prompt {
  position: absolute;
  left: 0;
  bottom: -20px;
  display: none;
}

.modal {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  display: none;
  overflow: hidden;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.666);
}

.modal__outher {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 50%;
  height: 50%;
  background: white;
  transform: translate(-50%, -50%);
}

.modal__inner {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 40px;
  height: 100%;
}

.modal__title {
  font-size: 40px;
  text-align: center;
}

.modal__close {
  position: absolute;
  right: 20px;
  top: 16px;
  font-size: 40px;
  cursor: pointer;
}

.modal__column {
  display: flex;
  justify-content: space-between;
}

.modal__column:not(:last-child) {
  margin-bottom: 8px;
}

.modal__name {
  font-size: 24px;
}

.modal__counter {
  display: flex;
  font-size: 16px;
  align-items: center;
}

.modal__count {
  margin: 0 8px;
}

.btn-counter {
  border: 1px solid #000;
  padding: 5px 10px;
  cursor: pointer;
}
